                <div>
                    <fieldset>
                        <legend>选择你应聘的岗位</legend>
                        <ul>
                            <li>
                                <label for="Java">Java</label>
                                <input
                                    type="radio"
                                    checked
                                    id="Java"
                                    name="user_profession"
                                    value="Java"
                                >
                            </li>
                            <li>
                                <label for="front">Front End</label>
                                <input
                                    type="radio"
                                    id="front"
                                    name="user_profession"
                                    value="front"
                                >
                            </li>
                            <li>
                                <label for="Ai">Ai</label>
                                <input
                                    type="radio"
